import popupLayer from '../../components/popup-layer/popup-layer.vue'
export default {
  name: 'App',
  props: {value:'',},
  data() {
    return {
        active: 0,
		cate: 2
    }
  },
  components: {
    popupLayer
  },
  methods: {
      gotoDetail(){
          this.$router.push({name: 'detail'})
      },
	  changeCate(){
		  this.cate = this.cate == 1 ? 2 : 1
	  },
	  change(){
		  this.active = 3;
		  this.$refs.popupLayer.show();
	  }
  }
}
